*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
}

.main-nav{
    width: 100%;
    background-color: #000;
    color: #fff;
}

.main-nav__list{
    padding: 15px 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.main-nav__item{
    margin: 0 25px;
}

.nav-item__link{
    color: #fff;
    text-decoration: none;
    transition: color 0.5s;
}

.home-arizona{
    color: #fff;
}

.arizona{
    width: 100%;
    height: 100vh;

    background-size: cover;
    background-position: center;
    background-attachment: fixed;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.arizona__header{
    width: 1100px;
    height: 250px;
    background-image: url(/images/arizona.jpg);
    background-size: cover;
    mask-image: url(/images/arizona.svg);
    mask-size: cover;
    mask-repeat: no-repeat;
    mask-position: center;

    -webkit-mask-image: url(/images/arizona.svg);
    -webkit-mask-size: cover;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: center;
    animation: pan 10s;
}

.arizona__subheader{
    font-size: 3rem;
    font-weight: 500;
    font-style: italic;
    animation-name: fadein;
    animation-duration: 20s;
}

@keyframes pan{
    0%{
        opacity: 0;
        background-position: 0 0;
    }
    50%{
        opacity: 1;
        background-position: 100% 0;
    }
}

@keyframes fadein{
    from{
        color: transparent;
    }
    to{
        color: #fff;
    }
}